#game-box {
    p {
        line-height: 30px;
    }
}

.message-tip {
    font-size: 20px;
    color: rgb(170, 9, 9);
    padding: 10px 0;
}

.record-box {
    overflow-y: auto;
    height: 300px;
    border: 1px solid #777;
    border-radius: 5px;
    padding: 5px;
    box-shadow: rgba(100, 100, 100, .3) 0 0 10px inset;
    p {
        line-height: 25px;
        color: #333;
        word-break: break-all;
    }
}

.input-box {
    a {
        display: inline-block;
        min-width: 60px;
        text-align: center;
        line-height: 30px;
        background-color: #ccc;
        border: 1px solid #767676; 
        text-decoration: none;
        color: #333;
    }
    input {
        height: 30px;
        outline: none;
        border: none;
        border-bottom: 1px solid;
    }
}
.btn {
    display: inline-block;
    min-width: 60px;
    text-align: center;
    line-height: 30px;
    background-color: #ccc;
    border: 1px solid #767676; 
    text-decoration: none;
    color: #333;
}